
.back-btn {
  display: block;
  margin: 20px auto;
  padding: 20px 20px;
  font-size: 1.2rem;
  color: white;
  background-color: #2ba94a; 
  border: none;
  border-radius: 6px;
  cursor: pointer;
  width: fit-content;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
  transition: background-color 0.3s ease;
}
  
/* Hover effect */
.back-btn:hover {
  background-color: #30c04f;
  transform: scale(1.05); 
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); 
}

/* Disabled State*/
.back-btn:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
  transform: none;
}